<template>
    <div class="modal fade" id="tagPlusModal" data-backdrop="static" data-keyboard="false" tabindex="-1" role="dialog" aria-labelledby="staticBackdropLabel" aria-hidden="true">
      <div class="modal-dialog modal-dialog-centered">
        <div class="modal-content">
          <!-- div class="modal-header">
            <h5 class="modal-title" id="staticBackdropLabel">新增</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div -->
          <div class="modal-body">
            <tag-plus-form ref="plusForm" @plus_Father="_plus"></tag-plus-form>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-dismiss="modal">取消</button>
            <button type="button" class="btn btn-main" @click="submitTagPlus">新增</button>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
import conn from '@/service/conn'
import tool from '@/common/es/tool'
import TagPlusForm from './TagPlusForm.vue'

export default {
  components: { TagPlusForm },
  name: 'TagUpdate',
  data() {
    return {
      tag: {}
    }
  },
  props: {
    tagRec: {
      type: Object
    }
  },
  created() {
    this.tag = this.tagRec
  },
  openModal() {
    tool.showModal('tagUpdateModal')
  },
  methods: {
    submitTagPlus() {

      this.$refs.plusForm.submitForm()
    },

    _signelRefreash() {
      // console.log('新增成功！！！重刷新表單')
      this.$emit('signelRefreash_Father')
    },
    _plus(data) {

      // console.log('data =', data)
      data['status'] = 'True'
      conn.tag.add(data).then(res => {
        if (conn.judge_response(res)) {
          
          tool.showToast('新增成功！！！', 2100)
          setTimeout(() => {
            tool.closeAllModal()

            this._signelRefreash()
          }, 700)
        }
      })
    }
  }
}
</script>

<style lang="sass" scoped>

</style>


