<template>
  
  <table class="table m-0 table-top-line-none">
    <tr class="text-left" v-for="(v, index) in tags" :key="v.id"  :id="`tag_tr_` + v.id">
      <td width="10%" class="text-accent">{{ index + 1 }}</td>
      <td width="25%">{{ v.named }}</td>
      <td width="20%">
        <div class="tag-color" :style="{ 'background': v.color }"></div>&nbsp;&nbsp;{{ v.color }}
      </td>
      <td width="30%">{{ ser_timed(v.add_time) }}</td>
      
      <td width="10%" class="text-center">

          <i @click="option(v.id, 'trash')" class="float-right">
            <remove></remove>
          </i>
          
          <i @click="option(v.id, 'update')" class="float-right">
            <pencil></pencil>
          </i>
      </td>
    </tr>
  </table>

</template>

<script>
import $ from 'jquery'
import service from '@/service'
import view from '@/common/es/view'
import tool from '@/common/es/tool'
import Remove from '../../../../components/Option/Remove.vue'
import Pencil from '../../../../components/Option/Pencil.vue'

export default {
  components: { Remove, Pencil },
  name: 'TagViewBody',
  props: {
    tags: {
      type: Array
    }
  },
  methods: {
    ser_timed(v) {
      return tool.timed.ser_timed(v, true, true)
    },
    option(id, option) { this.$emit('option_Father' , id, option) },
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"

</style>


