<template>
  <full-height-layout>
    <div slot="layout_title">標簽</div>

    <tag-view-top slot="layout_inner_head"></tag-view-top>

    <tag-view-body 
      :tags="tagList" 
      v-if="tagList" 
      slot="layout_inner_body" 
      @option_Father="option"></tag-view-body>

      <empty v-else-if="isLoading" slot="layout_inner_body"></empty>

      <transition v-else name="fade" slot="layout_inner_body">
        <loading></loading>
      </transition>


    <div slot="layout_extra">
      <plus-fixed @plusDone_Father="plusDone"></plus-fixed>

      <tag-plus @signelRefreash_Father="locationUpdateData"></tag-plus>

      <!--trash @trashItem="_trash"></trash-->

      <custom-modal
        :id_prefix="'trash_tag'"
        :title="'提示'"
        :txt="'刪除標簽為敏感操作，您確定要刪除它嗎？'"
        
        @done_Father="_trash"
      >
        <span slot="submit_text">確認刪除</span>
      </custom-modal>

      <tag-updated :tag="activeTag" ref="tagUpdateREF"  @signelRefreash_Father="locationUpdateData"></tag-updated>
    </div>

  </full-height-layout>
</template>

<script>
import tool from '@/common/es/tool'
import conn from '@/service/conn'
import conn_util from '@/common/es/conn_util'

import FullHeightLayout from '@/components/Layout/FullHeightLayout.vue'
import TagViewTop from './Top/TagViewTop.vue'
import TagViewBody from './Body/TagViewBody.vue'


import PlusFixed from '../../../components/Option/PlusFixed.vue'
import TagPlus from '../Plus/TagPlus.vue'
import Trash from '../../../components/Modal/Trash.vue'
import TagUpdated from '../Update/TagUpdated.vue'
import Empty from '../../../components/Shimmer/Empty.vue'
import Loading from '../../../components/Shimmer/Loading.vue'
import CustomModal from '../../../components/Modal/CustomModal.vue'

export default {
  components: { TagViewTop, FullHeightLayout, TagViewBody, PlusFixed, TagPlus, Trash, TagUpdated, Empty, Loading, CustomModal },
  name: 'TagView',
  data() {
    return {
      tagList: null,

      activeId: null,
      activeTag: null,

      activeURL: '',
      isLoading: false
    }
  },
  created() {
    window.addEventListener('beforeunload', e => this.beforLoad(e))
  },
  destroyed() {
    window.removeEventListener('beforeunload', e => this.beforLoad(e))
  },
  mounted() {
    this.getTag()
    /*
    conn.tag.getList_Tag().then(res => {
      this.tagList = res
    }).catch(err => {
          tool.showToast('网络连接失败。')
        })
    */
  },
  beforeDestroy() {
    // alert('销毁组件')
  },
  methods: {
    async getTag() {
      this.tagList = null
      const res = await conn.tag.getList_Tag()
      if (res) {
        this.tagList = res

        setTimeout(() => {
          this.isLoading = true
        }, 900);
      } else {
        tool.showToast('网络连接失败。')
      }
    },

    goPage(path) {

      this.$router.push(path)

      this.$store.state.iframeSrc = this.$route.meta.link
      this.$store.state.iframeTitle = this.$route.meta.title
    },

    beforLoad(e) {
      // console.log('Router =', this.$route.path)
      // console.log('刷新了，或要关闭。')
    },

    plusDone() {
      tool.showModal('tagPlusModal')
    },
    locationUpdateData() {
      this.getTag()
      // this.loadData(this.activeURL)
    },

    option(id, option) {
      this.activeId = id
      if (option == 'trash') {
        
        tool.showModal('trash_tagModal')
      } 
      else if (option == 'update') {
        conn.tag.getOne(id).then(res => {
          if (conn.judge_response(res)) {
            this.activeTag = res
            this.$refs.tagUpdateREF.locationUpdateData(res)
            tool.showModal('tagUpdateModal')
          }
        })
      }
    },
    _trash() {
      const _this = this
      tool.closeModal('trashModal') 

      if ((this.activeId != null) && (this.activeId > 0)) {

        // console.log('activeId =', this.activeId)

        conn.tag.delete(this.activeId).then(res => {
          if (conn.judge_response(res)) {
            
            tool.showToast('刪除成功！！')
            tool.hideEle('tag_tr_' + this.activeId)

            setTimeout(() => {
              this.getTag()
            }, 400)
            _this.isLoading = false
          }
        })

      }
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/listing.sass"

</style>


