<template>
    <div class="modal fade" id="tagUpdateModal"  data-keyboard="false" tabindex="-1" role="dialog" aria-labelledby="staticBackdropLabel" aria-hidden="true">
      <div class="modal-dialog modal-dialog-centered">
        <div class="modal-content">
          
          <div class="modal-body">
            <tag-update-form ref="updateFormREF" @update_Father="update"></tag-update-form>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-dismiss="modal">取消</button>
            <button class="btn btn-main" @click="submitTagPlus">保存</button>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
import conn from '@/service/conn'
import tool from '@/common/es/tool'
import TagUpdateForm from './TagUpdateForm.vue'

export default {
  components: { TagUpdateForm },
  name: 'TagUpdate',
  data() {
    return {
      tag_data: {}
    }
  },
  props: {
    
  },
  methods: {
    submitTagPlus() {

      this.$refs.updateFormREF.submitForm()
    },
    validateSame(data) {

      return true
    },
    update(data) {
      
      const res = this.validateSame(data) 

      const id = data.id

      if (res) {
        // console.log('Data =', data, id)
        conn.tag.update(data, id).then(res => {
          if (conn.judge_response(res)) {
            tool.showToast('修改成功！！！', 1900)
            setTimeout(() => {
              tool.closeAllModal()
              this.$emit('signelRefreash_Father')
            }, 700)
          }
        })
      }
      
    },
    locationUpdateData(data) {
      this.$refs.updateFormREF.locationData(data)
    }
  }
}
</script>

<style lang="sass" scoped>

</style>


