<template>
    
  <div class="modal-form-wrapper">
    <form>
      <div class="form-group">
        <label for="named">名稱</label>
        <input type="text" class="form-control" id="named" placeholder="標簽名稱" v-model="named">
        <small id="namedTip" class="form-text text-muted">{{ tip_named }}</small>
      </div>
      <div class="form-group">
        <label for="color">顏色</label>
        <div class="row">
            <div class="col-3">

                <div class="color-card"
                    :style="{ 'background': res_color }"
                >
                    （標簽）
                </div>
            </div>
            <div class="col-4">
               
                <div class="input-group">
                    <div class="input-group-prepend">
                    <div class="input-group-text">#</div>
                    </div>
                     <input type="text" class="form-control" id="color" placeholder="十六進製" v-model="color">
                </div>
            </div>
            <div class="col-2">

            </div>
            <div class="col-3">
                <button type="button" class="btn btn-secondary" @click="randomColor">
                    <i class="fas fa-dice-five"></i>
                    隨機生成</button>
            </div>
        </div>
        <small id="colorTip" class="form-text text-muted">{{ tip_color }}</small>
      </div>
    </form>
  </div>
</template>

<script>
import tool from '@/common/es/tool'
import conn from '@/service/conn'
import view from '@/common/es/view'
import validate from '@/common/es/validate'

    export default {
        name: '',
        data() {
            return {
                
                named: '',
                tip_named: '',

                color: 'f7f7f7',
                tip_color: '',
            }
        },
        computed: {

            res_color() {
                return this.rebuildColor()
            }
        },
        methods: {
            rebuildColor() {
                let res = ''
                if (this.color) {
                    if (!this.color.startsWith('#')) {
                        res = '#'
                    }
                    res += this.color
                }
                return res
            },

            tipClose(area) {
                setTimeout(() => {
                    if (area == 'named') {
                        this.tip_named = ''
                    }
                    else if (area == 'color') {
                        this.tip_color = ''
                    }
                }, 4200)
            },
            // 
            tipForm(res, area) {
                let show = ''
                if (res != true) {
                    show = res
                }
                // 
                if (area == 'num') { this.tip_num = show }
                else if (area == 'named') { this.tip_named = show }
                else if (area == 'color') { this.tip_color = show }

                this.tipClose(area)

            },

            validateForm() {
                let allow = true 
                
                res = validate.val_empty(this.named, '標簽名稱')
                if (res == true) {
                    res = validate.val_char(this.named, '標簽名稱')
                    this.tipForm(res, 'named')
                } else { this.tipForm(res, 'named') }
                if (res != true) { allow = false }
                
                let res = validate.val_empty(this.rebuildColor(), '顏色')
                if (res == true) {
                    res = validate.val_color(this.rebuildColor(), '顏色')
                    this.tipForm(res, 'color')
                } else { this.tipForm(res, 'color') }
                if (res != true) { allow = false }

                return allow
            },

            randomColor() {
                const res = tool.colors[ 
                    Math.floor(
                        (Math.random() * tool.colors.length)
                    ) 
                ]

                this.color = res
            },

            _validateSame(data, id = 0) {
                const condition = {
                    named: data.named,
                }
                return new Promise((resolve, reject) => {
                    conn.tag.getList_Tag(condition, true).then(res => {
                        
                        if (res.length > 0) {
                            

                                const e = []
                                for (const i of res) {
                                    
                                    if (i.named == data.named) {
                                        e.push(i)
                                    }
                                }
                                if (e.length > 0) { resolve(false) }
                                
                        }
                        resolve(true)
                    }).catch(err => {
                        reject(false)
                        tool.showToast('網絡連接失敗。')
                    })
                })
            },
            _submit(data) {

                this._validateSame(data).then(res => {
                    if (res) {
                        this.$emit('plus_Father', data)
                    } else {
                        tool.showToast('已存在相同名稱的標簽了。')
                    }
                })
            },
            submitForm() {

                const submit = this.validateForm()
                if (submit == true) {

                    const data = {
                        named: this.named,
                        color: this.rebuildColor(),
                    }

                    this._submit(data)
                }
            }
        }
    }
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
    
</style>


